/* globalStyles.css */
* {
  margin: 0; /* Zero out margin for all elements */
  padding: 0; /* Zero out padding for all elements */
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
}

body {
  margin: 0; /* Zero out margin */
  padding: 0; /* Zero out padding */
  overflow-x: hidden; /* Prevent horizontal overflow */
}

$app-background-color: #e9e9e9;
$app-text-color: #000000;

.app-container {
  position: relative; /* For positioning the pseudo-element */
  height: auto;
  width: 100%;
  transition: background 0.3s ease-in-out;
  overflow: hidden; /* Prevent overflow from blurred content */

  /* Set the background image */
  background-image: url('../../images/jjk.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;

  /* Create a blurred overlay */
  &::before {
    content: ""; // Required para makabuo ng pseudo-element
    position: absolute; // Iposisyon ito sa loob ng .app-container
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.700); // Adjust the rgba value for desired transparency (0: fully transparent, 1: fully opaque)
    backdrop-filter: blur(10px); /* Blur effect */
    z-index: 0; // Ilagay ito sa likod ng content
    transition: background 0.3s ease-in-out; /* Smooth transition for the overlay */
  }

  /* Default light theme styles */
  &.light {
    color: white; /* Ensures text contrast on dark background */
    // background: transparent;
  }

  /* Dark theme styles */
  &.dark {
    color: white; /* Ensures text contrast on dark background */
    // background: transparent;
  }
  
  .header {
    display: flex;
    justify-content: flex-end;
    padding: 20px;
    position: relative; /* Positioning for child elements above the blur */
  
    /* Float Button Styling */
    .float-button {
      display: flex; /* Para ma-center ang icon */
      align-items: center; /* I-center vertically ang icon */
      justify-content: center; /* I-center horizontally ang icon */
      border-radius: 50%; /* Round button */
      cursor: pointer;
      position: fixed; /* For possible inner elements positioning */
      border: none;
      background-color: rgb(255, 255, 255); /* Light background for visibility */
      width: 30px; /* Define a fixed width for the button */
      height: 30px; /* Define a fixed height for the button */
      transition: background-color 0.3s ease, transform 0.3s ease; /* Smooth transitions */

      /* Styling for the icon */
      .theme-icon {
        transition: color 0.3s ease; /* Smooth color transition */
      }
    }
  }
}
